.blurb {
  padding: 1rem;
  border: 1px solid #098266;
  border-radius: 3px;
  box-shadow: inset 0px 0px 138px #033748;
  color: #ddd;
  text-align: justify;
  font-size: 1.5rem;
  background-color: rgba(3, 40, 51, 0.5);
}

.blurb a {
  color: #6abaff;
  text-decoration: none;
}

.blurb p {
  margin: 0.5rem 0 1rem 0;
  text-align: center;
}

.portrait {
  width: 240px;
  margin: 1rem auto;
  display: block;
  border-radius: 440px;
  border: 2px solid #0ba1a6;
  box-shadow: 0 6px 80px #000f13;
}

.skills {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
  max-width: 48rem;
  margin: auto;
}

.skills > * {
  margin-left: 1rem;
  margin-right: 1rem;
}

@media (min-width: 426px) {
  .skills > * {
    margin-left: 2rem;
    margin-right: 2rem;
  }
}
