.container {
  background-color: #022834;
  padding-bottom: 10px;
  padding-top: 10px;
  border-bottom: 1px solid #0a5b5e;
  position: relative;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
  -ms-flex-direction: column;
  flex-direction: column;
  opacity: 0.9;
  padding: 0;
}

.links {
  padding: 4px;
  background-color: #119e8b;
  border-radius: 6px;
  display: flex;
  align-items: center;
  width: 100%;
  justify-content: center;
}

.links svg,
.links img {
  filter: invert();
}

.links a {
  height: 2rem;
  width: 2rem;
  padding: 0 2px;
}

.icon {
  height: 2rem;
  width: 2rem;
  border-radius: 6px;
}

.inner_container {
  flex: 1 1 auto;
  justify-content: space-between;
  display: flex;
  flex-wrap: wrap;
  max-width: 868px;
  margin: auto;
  width: 100%;
}

.demiacle {
  color: white;
  font-family: -apple-system, system-ui, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue',
    Arial, sans-serif !important;
  line-height: 2.25rem;
  font-size: 1.6rem;
  text-decoration: none;
  text-shadow: 1px 1px 1px black;
  letter-spacing: -0.05rem;
}
.demiacle_container,
.links {
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
  margin-left: 0.5rem;
  margin-right: 0.5rem;
}
.demiacle_container {
  text-align: center;
  width: 100%;
  margin-bottom: 0;
}

.transparent {
  opacity: 0.4;
}

@media (min-width: 426px) {
  .demiacle_container {
    text-align: left;
    width: unset;
  }
  .links {
    width: unset;
  }
}
